import React from 'react';
import './area-guide.styles.scss';

const AreaGuideCard = ({ areaGuide }) => {
  return (
    <div className="area-guide-card" data-aos="fade-up">
      <div
        className="image"
        style={{ backgroundImage: `url(${areaGuide.imageURL})` }}
      ></div>
      <div className="content">
        <h3>{areaGuide.name}</h3>
        <h6>
          <i className="fas fa-map-marker"></i> {areaGuide.location}
        </h6>
      </div>
    </div>
  );
};

export default AreaGuideCard;
