import React from 'react';
import './read-more-button.styles.scss';

const ReadMoreButton = ({ ourPasson, children }) => {
  return (
    <button className={`${ourPasson ? 'our-passon' : ''} read-more-button`}>
      {children} <i className="fas fa-arrow-right"></i>
    </button>
  );
};

export default ReadMoreButton;
