import React from 'react';
import './apartment.styles.scss';

const ApartmentCard = ({ aItems }) => (
  <div className="card" data-aos="fade-up" data-aos-easing="ease-out-cubic">
    <div
      className="image"
      style={{ backgroundImage: `url(${aItems.imageURL})` }}
    >
      <div className="img-footer">
        <h2>{aItems.price}</h2>
        <h6>
          <i className="fas fa-map-marker-alt"></i> {aItems.location}
        </h6>
      </div>
      <div className="heart">
        <i className="far fa-heart"></i>
      </div>
    </div>
    <div className="content">
      <h6>{aItems.title}</h6>
      <h3>{aItems.desc}</h3>
    </div>
    <div className="footer-card">
      <ul>
        <li>
          <i className="fas fa-bath"></i> {aItems.bathroom}
        </li>
        <li>
          <i className="fas fa-bed"></i> {aItems.bedroom}
        </li>
        <li>
          <i className="fas fa-hashtag"></i> {aItems.sqft}
        </li>
      </ul>
    </div>
  </div>
);

export default ApartmentCard;
