import React from 'react';
import AreaGuide from '../../components/area-guide/area-guide.component';
import Background from '../../components/background/background.component';
import CustomButton from '../../components/custom-button/custom-button.component';
import Bg from '../../components/bg/bg.component';
import './area-guidepage.styles.scss';

const AreaGuidePage = (props) => {
  return (
    <div className="area-guide-page">
      <Background pageName="Area Guides">Area Guides</Background>
      <AreaGuide areaGuides={props.areaGuides} />
      <Bg>
        <div className="content">
          <h1>HOW MUCH IS YOUR PROPERTY WORTH?</h1>
          <h5>Get your free online valuation</h5>
          <CustomButton>Instant Valuation</CustomButton>
        </div>
      </Bg>
    </div>
  );
};

export default AreaGuidePage;
