import React from 'react';
import Title from '../title/title.component';
import CustomButton from '../../components/custom-button/custom-button.component';
import './contact-us.styles.scss';

const ContactUs = () => {
  return (
    <div className="contact-us">
      <iframe
        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d56516.31625950954!2d85.29111325668332!3d27.708955944412793!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x39eb198a307baabf%3A0xb5137c1bf18db1ea!2sKathmandu%2044600!5e0!3m2!1sen!2snp!4v1609927650180!5m2!1sen!2snp"
        title="map"
      ></iframe>
      <Title>Get In Touch</Title>
      <div className="contact-form">
        <form>
          <textarea
            name="message"
            placeholder="Enter Message"
            cols="30"
            rows="9"
          ></textarea>
          <input type="text" className="name" placeholder="Enter your name" />
          <input type="text" className="email" placeholder="Enter your email" />
          <input type="text" className="subject" placeholder="Enter Subject" />
          <CustomButton>Send Message</CustomButton>
        </form>
        <div className="info">
          <ul>
            <li>
              <i className="fas fa-home"></i>
              <div>
                <h5>Buttonwood, California.</h5>
                <h6>Rosemead, CA 91770</h6>
              </div>
            </li>
            <li>
              <i className="fas fa-tablet-alt"></i>
              <div>
                <h5>00 (440) 9865 562</h5>
                <h6>Mon to Fri 9am to 6pm</h6>
              </div>
            </li>
            <li>
              <i className="fas fa-envelope"></i>
              <div>
                <h5>support@colorlib.com</h5>
                <h6>Send us your query anytime!</h6>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default ContactUs;
