import React, { useState } from 'react';
import Background from '../../background/background.component';
import PageNotFound from '../../../pages/page-not-found/page-not-found.component';
import './apartment-details-page.styles.scss';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/components/navigation/navigation.scss';
import 'swiper/components/pagination/pagination.scss';
import 'swiper/components/scrollbar/scrollbar.scss';
import 'swiper/swiper.scss';
import SwiperCore, {
  Navigation,
  Pagination,
  Scrollbar,
  Thumbs,
  A11y,
} from 'swiper';
SwiperCore.use([Navigation, Pagination, Scrollbar, Thumbs, A11y]);

const ApartmentDeatilsPage = (props) => {
  const [thumbsSwiper, setThumbsSwiper] = useState(null);
  return props.cardId !== null ? (
    props.apartments.map((apartment, index) => {
      return props.cardId === apartment.id ? (
        <div className="details-page" key={index}>
          <Background pageName="details-page">
            {apartment.innerDetails.place}
          </Background>
          <div className="container-fluid">
            <div className="row">
              <div className="col-md-9">
                <div className="images">
                  <Swiper
                    thumbs={{ swiper: thumbsSwiper }}
                    navigation
                    pagination={{ clickable: true }}
                    scrollbar={{ draggable: true }}
                  >
                    {apartment.innerDetails.imageURL.map((img) => {
                      return (
                        <SwiperSlide
                          id={img.id}
                          style={{
                            backgroundImage: `url(${img.imgs})`,
                            height: '25rem',
                          }}
                        ></SwiperSlide>
                      );
                    })}
                  </Swiper>
                  <Swiper
                    spaceBetween={10}
                    slidesPerView={4}
                    onSwiper={setThumbsSwiper}
                    watchSlidesVisibility
                    watchSlidesProgress
                  >
                    {apartment.innerDetails.imageURL.map((img) => {
                      return (
                        <SwiperSlide
                          id={img.id}
                          style={{
                            backgroundImage: `url(${img.imgs})`,
                          }}
                        ></SwiperSlide>
                      );
                    })}
                  </Swiper>
                </div>
              </div>
              <div className="col-md-3">
                <div className="details">
                  <h2>{apartment.innerDetails.place}</h2>
                  <h6>
                    <i className="fas fa-map-marked"></i>
                    {apartment.innerDetails.location}
                  </h6>
                  <br />
                  <h4>{apartment.innerDetails.price}</h4>

                  <br />
                  <h3>Property Highlights</h3>
                  <p>{apartment.innerDetails.desc}</p>
                  <ul>
                    <li>
                      <i className="fas fa-bath"></i>{' '}
                      {apartment.innerDetails.bathroom}
                    </li>
                    <li>
                      <i className="fas fa-door-open"></i>{' '}
                      {apartment.innerDetails.bathroom}
                    </li>
                    <li>
                      <i className="fas fa-faucet"></i>{' '}
                      {apartment.innerDetails.bathroom}
                    </li>
                    <li>
                      <i className="fas fa-bed"></i>{' '}
                      {apartment.innerDetails.bedroom}
                    </li>
                    <li>
                      <i className="fas fa-hashtag"></i>{' '}
                      {apartment.innerDetails.sqft}
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      ) : null;
    })
  ) : (
    <PageNotFound />
  );
};

export default ApartmentDeatilsPage;
