import React from 'react';
import ApartmentCard from './apartment-card.component';
import { Link } from 'react-router-dom';
import './apartment.styles.scss';

const ApartmentLists = (props) => {
  return (
    <div className="card-list ipad">
      {window.location.pathname === '/'
        ? props.apartment
            .filter((item, idx) => idx < 4)
            .map((aItems) => {
              return (
                <div key={aItems.id}>
                  <ApartmentCard aItems={aItems} />
                </div>
              );
            })
        : props.apartment.map((aItems) => {
            return (
              <div onClick={() => props.setCardId(aItems.id)} key={aItems.id}>
                <Link to="/apartment-details-page">
                  <ApartmentCard aItems={aItems} />
                </Link>
              </div>
            );
          })}
    </div>
  );
};

export default ApartmentLists;
