import React from 'react';
import Title from '../title/title.component';
import ReadMoreButton from '../read-more-button/read-more-button.component';
import ApartmentLists from './apartment-list.component';
import { Link } from 'react-router-dom';
import './apartment.styles.scss';

const Apartment = (props) => {
  return (
    <div className="apartment">
      <div className="heading">
        <Title>
          Provide The <br /> Beautiful Apartment
        </Title>
        <Link to="/rent">
          <ReadMoreButton>Read More</ReadMoreButton>
        </Link>
      </div>
      <ApartmentLists apartment={props.apartment} setCardId={props.setCardId} />
    </div>
  );
};

export default Apartment;
