import React from 'react';
import PropertyCard from './property-card.component';
import { Link } from 'react-router-dom';
import './properties.styles.scss';

const PropertyList = (props) => {
  return (
    <div className="card-list">
      {window.location.pathname === '/'
        ? props.propertie
            .filter((item, idx) => idx < 8)
            .map((details) => {
              return (
                <div
                  onClick={() => props.setCardId(details.id)}
                  key={details.id}
                >
                  <Link to="/property-details-page">
                    <PropertyCard detail={details} />
                  </Link>
                </div>
              );
            })
        : props.propertie.map((details) => {
            return (
              <div onClick={() => props.setCardId(details.id)} key={details.id}>
                <Link to="/property-details-page">
                  <PropertyCard detail={details} />
                </Link>
              </div>
            );
          })}
    </div>
  );
};

export default PropertyList;
