import React from 'react';
import Carousel from 'react-elastic-carousel';
import './footer.styles.scss';

const breakPoints = [
  { width: 1, itemsToShow: 1 },
  { width: 550, itemsToShow: 1 },
  { width: 768, itemsToShow: 1 },
  { width: 1200, itemsToShow: 1 },
];

const Footer = () => {
  return (
    <div className="footer">
      <div className="content-list">
        <div className="about-sec">
          <div className="titles">About Us</div>
          <div className="image"></div>
          <div className="logo">LOGO</div>
          <p>
            Lorem Ipsum is simply dummy text of the printing and typesetting
            industry. Lorem Ipsum has been the industry's standard dummy text
            ever since the 1500s, when an unknown printer took a galley of type
            and scrambled it to make a type specimen book.
          </p>
        </div>
        <div className="hours">
          <div className="titles">Hours</div>
          <ul>
            <li>
              <span>Monday - Friday</span> <span>09 AM - 19 PM</span>
            </li>
            <li>
              <span>Saturday</span> <span>09 AM - 14 PM</span>
            </li>
            <li>
              <span>Sunday</span> <span>Close</span>
            </li>
          </ul>
          <h6>
            <i className="fas fa-phone"></i> +45 677 8993000 223
          </h6>
          <h6>
            <i className="fas fa-envelope"></i> office@template.com
          </h6>
          <h6>
            <i className="fas fa-map-marker"></i> Main Str. no 45-46, Los
            Angeles, CA
          </h6>
        </div>
        <div className="useful-links">
          <div className="titles">Useful Links</div>
          <ul>
            <li>
              <span>Home</span> <span>About Us</span>
            </li>
            <li>
              <span>About Us</span> <span>Services</span>
            </li>
            <li>
              <span>Properties</span> <span>Listings</span>
            </li>
            <li>
              <span>Testimonials</span> <span>Properties</span>
            </li>
            <li>
              <span>Blog</span> <span>Testimonials</span>
            </li>
            <li>
              <span>Contact</span> <span>Elements</span>
            </li>
            <li>
              <span>FAQ</span> <span></span>
            </li>
          </ul>
        </div>
        <div className="featured-propertier">
          <div className="titles">Featured Propertier</div>
          <Carousel breakPoints={breakPoints}>
            <div className="img-slide img1" />
            <div className="img-slide img2" />
            <div className="img-slide img3" />
            <div className="img-slide img4" />
          </Carousel>
        </div>
      </div>
      <p>Copyright ©2021 All rights reserved</p>
    </div>
  );
};

export default Footer;
