import React from 'react';
import Background from '../../components/background/background.component';
import './rentpage.styles.scss';
import Apartment from '../../components/apartment/apartment.component';
import { SearchBox } from '../../components/search-box/search-box.component';

const RentPage = (props) => {
  return (
    <div>
      <Background pageName={'Rent'}>Apartment</Background>
      <SearchBox
        placeholder="search by location"
        handleChange={props.handleChange}
      />
      <Apartment apartment={props.apartments} setCardId={props.setCardId} />
    </div>
  );
};

export default RentPage;
