import React from 'react';
import CustomButton from '../custom-button/custom-button.component';
import './dream-house.styles.scss';

const DreamHouse = () => (
  <div className="dream-house">
    <div className="content" data-aos="zoom-in">
      <h1>Choose Your Dream House</h1>
      <h6>
        We can manage your dream building A small river named Duden flows by
        there place
      </h6>
      <CustomButton>Search Place</CustomButton>
    </div>
    <div className="bg"></div>
  </div>
);

export default DreamHouse;
