import React from 'react';
import ReadMoreButton from '../read-more-button/read-more-button.component';
import Title from '../title/title.component';
import { Link } from 'react-router-dom';
import Carousel from 'react-elastic-carousel';
import './our-passon.styles.scss';

const breakPoints = [
  { width: 1, itemsToShow: 1 },
  { width: 550, itemsToShow: 2 },
  { width: 768, itemsToShow: 3 },
  { width: 1200, itemsToShow: 4 },
];

const CardList = (props) => {
  return (
    <div className="card-list">
      <Carousel breakPoints={breakPoints}>
        {props.passonLists.map((item) => {
          return <Card items={item} key={item.id} />;
        })}
      </Carousel>
    </div>
  );
};

const Card = (props) => {
  return (
    <div className="box" data-aos="fade-up">
      <div className="box-item">
        <Link to="/about-us" className="icon">
          <i className={props.items.icon}></i>
        </Link>
        <h4>{props.items.title}</h4>
        <p>{props.items.desc}</p>
        <Link to="/about-us">
          <ReadMoreButton ourPasson>Read More</ReadMoreButton>
        </Link>
      </div>
    </div>
  );
};

