import React from 'react';
import { Switch, Route } from 'react-router-dom';
import Header from './components/header/header.component';
import Homepage from './pages/homepage/homepage.component';
import RentPage from './pages/rentpage/rentpage.component';
import SalesPage from './pages/salespage/salespage.component';
import Footer from './components/footer/footer.component';
import PropertyDetailsPage from './components/properties/property-details-page/property-details-page.component';
import ApartmentDeatilsPage from './components/apartment/apartment-details-page/apartment-details-page.componsnt';
import ContactPage from './pages/contactpage/contactpage.component';
import AboutPage from './pages/aboutpage/aboutpage.component';
import APARTMENT_DATA from './data/apartments.data';
import PROPERTIES_DATA from './data/properties.data';
import AREA_GUIDES from './data/area-guide.data';
import AOS from 'aos';
import './App.css';
import 'aos/dist/aos.css';
import AreaGuidePage from './pages/area-guidepage/area-guidepage.component';

class App extends React.Component {
  constructor() {
    super();

    this.state = {
      cardId: null,
      apartments: APARTMENT_DATA,
      properties: PROPERTIES_DATA,
      areaGuides: AREA_GUIDES,
      searchPropertise: '',
      searchApartments: '',
    };
  }

  setCardId = (id) => {
    this.setState({
      cardId: id,
    });
  };

  handleChange = (e) => {
    this.setState({ searchPropertise: e.target.value });
    this.setState({ searchApartments: e.target.value });
  };

  render() {
    AOS.init();
    const { properties, searchPropertise } = this.state;
    const filteredProperties = properties.filter((property) =>
      property.place.toLowerCase().includes(searchPropertise.toLowerCase())
    );

    const { apartments, searchApartments } = this.state;
    const filteredApartments = apartments.filter((apartment) =>
      apartment.title.toLowerCase().includes(searchApartments.toLowerCase())
    );
    return (
      <>
        <Header />
        <Switch>
          <Route exact path="/">
            <Homepage
              apartments={this.state.apartments}
              properties={this.state.properties}
              setCardId={this.setCardId}
            />
          </Route>
          <Route exact path="/rent">
            <RentPage
              apartments={filteredApartments}
              setCardId={this.setCardId}
              handleChange={this.handleChange}
            />
          </Route>
          <Route exact path="/sales">
            <SalesPage
              properties={filteredProperties}
              setCardId={this.setCardId}
              handleChange={this.handleChange}
            ></SalesPage>
          </Route>
          <Route exact path="/property-details-page">
            <PropertyDetailsPage
              cardId={this.state.cardId}
              properties={this.state.properties}
            />
          </Route>
          <Route exact path="/apartment-details-page">
            <ApartmentDeatilsPage
              cardId={this.state.cardId}
              apartments={this.state.apartments}
            />
          </Route>
          <Route exact path="/contact-us" component={ContactPage}></Route>
          <Route exact path="/about-us" component={AboutPage}></Route>
          <Route exact path="/area-guides">
            <AreaGuidePage areaGuides={this.state.areaGuides} />
          </Route>
        </Switch>
        <Footer />
      </>
    );
  }
}

export default App;
