import React from 'react';
import Title from '../title/title.component';
import PropertyList from './property-list.component';
import { Link } from 'react-router-dom';
import ReadMoreButton from '../read-more-button/read-more-button.component';
import './properties.styles.scss';

const Properties = (props) => {
  return (
    <div className="properties">
      <div className="heading">
        <Title>
          Our Featured <br /> Properties
        </Title>
        <Link to="/sales">
          <ReadMoreButton>Read More</ReadMoreButton>
        </Link>
      </div>
      <PropertyList propertie={props.propertie} setCardId={props.setCardId} />
    </div>
  );
};

export default Properties;
