import React from 'react';
import AreaGuideList from './area-guide-list.component';
import './area-guide.styles.scss';
import Title from '../title/title.component';

const AreaGuide = (props) => {
  return (
    <div className="area-guide">
      <Title>
        Not sure where <br /> to hang your <br /> hat?
      </Title>
      <AreaGuideList areaGuides={props.areaGuides} />
    </div>
  );
};

export default AreaGuide;
