import React from 'react';
import AreaGuideCard from './area-guide-card.component';
import './area-guide.styles.scss';

const AreaGuideList = (props) => {
  return (
    <div className="area-guide-list">
      {props.areaGuides.map((areaGuide) => (
        <AreaGuideCard areaGuide={areaGuide} key={areaGuide.id} />
      ))}
    </div>
  );
};

export default AreaGuideList;
