import React from 'react';
import CustomButton from '../custom-button/custom-button.component';
import Bg from '../bg/bg.component';
import { Link } from 'react-router-dom';
import './place-to-rent.styles.scss';

const PlaceToRent = () => {
  return (
    <div className="place-to-rent">
      <Bg>
        <div className="content">
          <h1>ARE YOU LOOKING FOR A PLACE TO RENT?</h1>
          <h5>Suspendisse dictum enim sit amet libero malesuada feugiat.</h5>
          <Link to="/rent">
            <CustomButton>Search</CustomButton>
          </Link>
        </div>
      </Bg>
    </div>
  );
};

export default PlaceToRent;
