import React from 'react';
import Slider from '../../components/slider/slider.component';
import Living from '../../components/living/living.component';
import OurPasson from '../../components/our-passon/our-passon.component';
import Apartment from '../../components/apartment/apartment.component';
import DreamHouse from '../../components/dream-house/dream-house.component';
import Stayhome from '../../components/stayhome/stayhome.component';
import Properties from '../../components/properties/properties.component';
import HappyCustomer from '../../components/happy-costomer/happy-customer.component';
import './homepage.styles.scss';

const Homepage = (props) => {
  return (
    <div className="home-page">
      <Slider />
      <Living />
      <OurPasson />
      <Apartment apartment={props.apartments} />
      <DreamHouse />
      <Stayhome />
      <Properties propertie={props.properties} setCardId={props.setCardId} />
      <HappyCustomer />
    </div>
  );
};

export default Homepage;
