import React from 'react';
import './properties.styles.scss';

const PropertyCard = (props) => {
  return (
    <div
      className="card"
      data-aos="fade-up"
      data-aos-easing="ease-out-cubic"
      data-aos-duration="6000"
    >
      <div
        className="image"
        style={{ backgroundImage: `url(${props.detail.imageURL})` }}
      >
        <div className="img-footer">
          <h5>{props.detail.price}</h5>
        </div>
      </div>
      <div className="content">
        <h5>{props.detail.place}</h5>
        <h6>
          <i className="fas fa-map-marked"></i>
          {props.detail.location}
        </h6>
        <p>{props.detail.desc}</p>
        <div className="footer-card">
          <ul>
            <li>
              <i className="fas fa-bath"></i> {props.detail.bathroom}
            </li>
            <li>
              <i className="fas fa-bed"></i> {props.detail.bedroom}
            </li>
            <li>
              <i className="fas fa-hashtag"></i> {props.detail.sqft}
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default PropertyCard;
